import { GoogleGenAI } from "@google/genai";
import { customPrompt } from "../../config.json";

const geminiRequest = async (prompt: string) => {
  const ai = new GoogleGenAI({
    apiKey: "AIzaSyBReVdS-BCWfBu3N9Y3_KDFiRZxfEWo7X8",
  });
  // PROMPT
  let finalPrompt =
    customPrompt +
    "Voici le prompt de l'utilisateur, r√©pond y en respectant les consignes pr√©c√©dentes: " +
    prompt;
  console.log(finalPrompt);
  const response = await ai.models.generateContent({
    model: "gemini-2.5-flash",
    config: {
      thinkingConfig: {
        thinkingBudget: -1,
      },
    },
    contents: finalPrompt,
  });
  const finalResponse = response.text;
  return finalResponse;
};

export default geminiRequest;
